<template>
  <div class="d-flex align-items-center v-checkbox">
    <VButton
      class="me-1 v-checkbox__square"
      :class="{
        'v-checkbox__square--checked': modelValue,
      }"
      @click="$emit('update:modelValue', !modelValue)"
    >
      <CheckIcon />
    </VButton>

    <div class="v-checkbox__content">
      <slot></slot>
    </div>
  </div>
</template>

<script>
import VButton from "@/components/buttons/VButton";
import CheckIcon from "@/components/icons/CheckIcon";

export default {
  name: "VCheckbox",
  components: { CheckIcon, VButton },
  props: {
    modelValue: Boolean,
  },
};
</script>

