import { identityObject } from "@/utils/DataUtils";

export function mapExternalResourceErrors(resource) {
  const notDate = !resource.date;
  const notRussianName = !resource.russianName;
  const notEnglishName = !resource.englishName;
  const notProjectNumber = !resource.projectNumber;
  const notContractNumber = !resource.contractNumber;
  const notRussianFullName = !resource.russianFullName;
  const notEnglishFullName = !resource.englishFullName;

  return identityObject({
    date: notDate ? "Введите дату договора" : undefined,
    projectNumber: notProjectNumber ? "Введите номер проекта" : undefined,
    contractNumber: notContractNumber ? "Введите номер контракта" : undefined,
    russianName: notRussianName
      ? "Введите название проекта на русском языке"
      : undefined,
    englishName: notEnglishName
      ? "Введите название проекта на английском языке"
      : undefined,
    englishFullName: notEnglishFullName
      ? "Введите полное наименование лица на английском языке"
      : undefined,
    russianFullName: notRussianFullName
      ? "Введите полное наименование лица на английском языке"
      : undefined,
  });
}
