<template>
  <FormsContainer>
    <TypographyWithInfo
      font-weight="heavy"
      font-style="header-1"
      class="mb-5"
      popover-position="bottom-right"
      >Исходный код Программы для ЭВМ
      <template v-slot:popover>
        <PopoverContent :width="926">
          <VTypography font-weight="medium"
            >Предоставьте, пожалуйста, активную ссылку или отдельный документ c
            исходным кодом Программы для ЭВМ в виде текста и, если возможно, со
            скриншотами., а также исполняемый файл Программы для ЭВМ.
          </VTypography>
        </PopoverContent>
      </template></TypographyWithInfo
    >
    <TypographyWithInfo font-weight="heavy" class="mb-5"
      >Файлы с исходным кодом Программы для ЭВМ
      <template v-slot:popover>
        <PopoverContent :width="350">
          <VTypography font-weight="medium">Подсказка </VTypography>
        </PopoverContent>
      </template></TypographyWithInfo
    >

    <div class="d-flex flex-column mb-5" v-if="files.length > 0">
      <TechnologyFileItem
        :file="file"
        :key="file.name"
        v-for="(file, index) in files"
        @delete="onDeleteClick(index)"
        class="source-code-form__file-item"
      />
    </div>

    <VTypography class="mb-1">Загрузить файлы</VTypography>

    <FilePicker
      :file-formats="['doc', 'docx', 'pdf']"
      file-limits="5 мб"
      :loading="loading"
      @change="onFilesSelected"
    />
  </FormsContainer>
</template>

<script>
import { mapActions, mapGetters, mapMutations } from "vuex";

import FilePicker from "@/components/ui/FilePicker";
import VTypography from "@/components/ui/Typography";
import FormsContainer from "@/components/forms/FormsContainer";
import PopoverContent from "@/components/popover/PopoverContent";
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";
import TechnologyFileItem from "@/components/technology/TechnologyFileItem";

export default {
  name: "SourceCodeForm",
  components: {
    FilePicker,
    VTypography,
    PopoverContent,
    FormsContainer,
    TechnologyFileItem,
    TypographyWithInfo,
  },
  computed: {
    ...mapGetters("sourceCode", {
      files: "getFiles",
    }),
  },
  data() {
    return {
      loading: false,
    };
  },
  methods: {
    ...mapMutations("sourceCode", ["removeFile"]),
    ...mapActions("sourceCode", ["uploadFiles"]),
    onDeleteClick(index) {
      this.removeFile(index);
    },
    onFilesSelected(files) {
      this.loading = true;

      this.uploadFiles(files).finally(() => {
        this.loading = false;
      });
    },
  },
};
</script>

<style scoped lang="scss">
.source-code-form__file-item {
  &:not(:last-child) {
    margin-bottom: 10px;
  }
}
</style>
