<template>
  <FormsContainer class="technology-form">
    <TypographyWithInfo
      font-weight="heavy"
      font-style="header-1"
      class="mb-5"
      popover-position="bottom-right"
      >Описание Технологии
      <template v-slot:popover>
        <PopoverContent :width="926">
          <VTypography font-weight="medium"
            >Пожалуйста, предоставьте активную ссылку на полное и подробное
            описание Технологии, раскрывающее как минимум:
          </VTypography>
          <VTypography font-weight="medium">
            1. Область техники, включая список ключевых слов;
          </VTypography>
          <VTypography font-weight="medium">
            2. Уровень техники, т.е. технологии, являющиеся аналогами
            Технологии, совпадающие с ней по назначению и наиболее близкие к ней
            по технической сущности;
          </VTypography>
          <VTypography font-weight="medium">
            3. Наименования активных участников рынка, использующих технологии,
            перечисленные в п. 2) выше, при изготовлении товаров, выполнении
            работ, оказании услуг и т.п.;
          </VTypography>
          <VTypography font-weight="medium">
            4. Совокупность признаков Технологии по сравнению с существующими
            изобретениями, полезными моделями, промышленными образцами;
          </VTypography>
          <VTypography font-weight="medium">
            5. Полное описание Технологии;
          </VTypography>
          <VTypography font-weight="medium">
            6. Технический результат и коммерческое применение Технологии.
          </VTypography>
          <VTypography font-weight="medium">
            При необходимости добавьте дополнительные сведения, графики,
            чертежи, рисунки и т.п.
          </VTypography>
          <VTypography font-weight="medium">
            Описание может быть предоставлено как на двух языках — русском и
            английском, — так и на одном из них по выбору Авторов. Если это не
            нарушает качество раскрытия, предоставьте описание на русском языке
            для потенциальной подачи российской или евразийской заявки и/или на
            английском языке для потенциальной подачи иностранных заявок и
            заявок по процедуре PCT. В противном случае описание будет
            переведено на требуемый язык с привлечением платных услуг,
            предоставляемых аккредитованными юридическими фирмами.
          </VTypography>
        </PopoverContent>
      </template></TypographyWithInfo
    >
    <TypographyWithInfo font-weight="heavy" class="mb-5"
      >Файлы с описанием Технологии, иные материалы
      <template v-slot:popover>
        <PopoverContent :width="350">
          <VTypography font-weight="medium">Подсказка </VTypography>
        </PopoverContent>
      </template></TypographyWithInfo
    >

    <div class="d-flex flex-column mb-5" v-if="files.length > 0">
      <TechnologyFileItem
        :file="file"
        :key="file.name"
        :disabled="!isEditableStatus"
        v-for="(file, index) in files"
        @delete="onDeleteClick(index)"
        class="technology-form__file-item"
      />
    </div>

    <VTypography class="mb-1">Загрузить файлы</VTypography>

    <FilePicker
      :file-formats="[
        'doc',
        'docx',
        'pdf',
        'xls',
        'xlsx',
        'zip',
        'rar',
        'jpg',
        'png',
      ]"
      file-limits="100 мб"
      :loading="loading"
      :disabled="!isEditableStatus"
      @change="onFilesSelected"
    />
  </FormsContainer>
</template>

<script>
import { mapActions, mapGetters, mapMutations } from "vuex";

import FilePicker from "@/components/ui/FilePicker";
import VTypography from "@/components/ui/Typography";
import FormsContainer from "@/components/forms/FormsContainer";
import PopoverContent from "@/components/popover/PopoverContent";
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";
import TechnologyFileItem from "@/components/technology/TechnologyFileItem";

export default {
  name: "TechnologyForm",
  components: {
    FilePicker,
    VTypography,
    PopoverContent,
    FormsContainer,
    TechnologyFileItem,
    TypographyWithInfo,
  },
  computed: {
    ...mapGetters("formInfo", ["isEditableStatus"]),
    ...mapGetters("technology", {
      files: "getFiles",
    }),
  },
  data() {
    return {
      loading: false,
    };
  },
  methods: {
    ...mapMutations("technology", ["removeFile"]),
    ...mapActions("technology", ["uploadFiles"]),
    onDeleteClick(index) {
      this.removeFile(index);
    },
    onFilesSelected(files) {
      this.loading = true;

      this.uploadFiles(files).finally(() => {
        this.loading = false;
      });
    },
  },
};
</script>

<style scoped lang="scss">
.technology-form__file-item {
  &:not(:last-child) {
    margin-bottom: 10px;
  }
}
</style>
