<template>
  <div
    ref="input-autocomplete"
    class="position-relative"
    :class="$attrs['root-class']"
    v-click-outside="onCLickOutside"
  >
    <TextInput v-bind="$attrs" @focus="focus = true">
      <template v-slot:popover>
        <slot name="popover"></slot>
      </template>
    </TextInput>

    <div
      v-if="$attrs.modelValue && items.length > 0 && focus"
      class="position-absolute text-input-autocomplete__list"
      :style="{ top: `${top}px` }"
    >
      <VButton
        :key="index"
        @click="onSelect(item)"
        v-for="(item, index) in items"
        class="px-2 text-input-autocomplete__list-item"
      >
        <VTypography font-weight="medium">{{ item.name?.russian }}</VTypography>
      </VButton>
    </div>
  </div>
</template>

<script>
import TextInput from "@/components/ui/TextInput";
import VButton from "@/components/buttons/VButton";
import VTypography from "@/components/ui/Typography";

export default {
  inheritAttrs: false,
  name: "TextInputAutocomplete",
  components: { VButton, VTypography, TextInput },
  emits: {
    select: () => true,
  },
  data() {
    return {
      top: 0,
      focus: false,
    };
  },
  mounted() {
    const ref = this.$refs["input-autocomplete"];

    if (ref) {
      this.top = ref.clientHeight;
    }
  },
  props: {
    list: {
      type: Array,
      default: () => [],
    },
  },
  computed: {
    items() {
      return this.list.filter(
        (x) => x.name.russian.toLowerCase().indexOf(this.$attrs.modelValue) >= 0
      );
    },
  },
  methods: {
    onCLickOutside() {
      this.focus = false;
    },
    onSelect(item) {
      this.$emit("select", item);

      this.focus = false;
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/mixins.scss";
@import "~@/assets/styles/variables.scss";

.text-input-autocomplete__list {
  left: 0;
  right: 0;
  height: 275px;
  overflow-y: auto;
  background-color: $gray3;
}

.text-input-autocomplete__list-item {
  @include transition(color);

  height: 50px;
  color: $white;

  &:hover {
    color: $green;
  }
}
</style>
