<template>
  <div class="d-flex">
    <VButton
      :key="item.key"
      v-for="item in list"
      :disabled="disabled"
      @click="$emit('update:modelValue', item.key)"
      class="d-flex user-select-none align-items-center radio-group__item"
      :class="{
        'radio-group__item--disabled': disabled,
        'radio-group__item--active': item.key === modelValue,
      }"
    >
      <div
        class="me-1 radio-group__item-dot"
        :class="{
          'radio-group__item-dot--disabled': disabled,
          'radio-group__item-dot--contrast': type === 'contrast',
        }"
      />

      <VTypography
        font-weight="medium"
        class="radio-group__item-title"
        :class="{
          'radio-group__item-title--disabled': disabled,
          'radio-group__item-title--contrast': type === 'contrast',
        }"
        >{{ item.title }}</VTypography
      >
    </VButton>
  </div>
</template>

<script>
import VButton from "@/components/buttons/VButton";
import VTypography from "@/components/ui/Typography";

export default {
  name: "RadioGroup",
  components: { VButton, VTypography },
  props: {
    items: Array,
    disabled: Boolean,
    modelValue: [String, Boolean, Number],
    type: {
      type: String,
      default: "default",
      validate(value) {
        return ["default", "contrast"].includes(value);
      },
    },
  },
  computed: {
    list() {
      if (this.disabled) {
        return this.items.filter((x) => x.key === this.modelValue);
      }

      return this.items;
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/mixins.scss";
@import "~@/assets/styles/variables.scss";

.radio-group__item {
  cursor: pointer;

  &:not(:last-child) {
    margin-right: 20px;
  }
}

.radio-group__item-dot {
  @include transition(border);

  width: 20px;
  height: 20px;
  border-radius: 10px;
  border: 2px solid $gray4;
}

.radio-group__item-dot--disabled {
  border-color: $gray4;
}

.radio-group__item-title {
  @include transition(color);
  color: $gray4;
}

.radio-group__item--disabled {
  cursor: default;
}

.radio-group__item--active {
  & > .radio-group__item-title {
    color: $gray1;
  }

  & > .radio-group__item-title--contrast {
    color: $white;
  }

  & > .radio-group__item-title--disabled {
    color: $gray4;
  }

  & > .radio-group__item-dot {
    border: 5px solid $gray1;
  }
  & > .radio-group__item-dot--contrast {
    border-color: $white;
  }
  & > .radio-group__item-dot--disabled {
    border: 2px solid $gray4;
  }
}
</style>
