<template>
  <div class="d-flex">
    <VButton
      v-bind="$attrs"
      :class="{ 'file-picker__button--disabled': disabled }"
      class="flex-fill position-relative me-2 file-picker__button"
    >
      <input
        type="file"
        role="button"
        :multiple="true"
        :disabled="loading || disabled"
        @change="$emit('change', $event.target.files)"
        :accept="fileFormats.join(',.')"
        class="position-absolute w-100 opacity-0 file-picker__file-input"
      />

      <VLoader v-if="loading" :size="16" />

      <VTypography
        v-if="!loading"
        font-weight="medium"
        class="file-picker__title"
        >Загрузить файлы</VTypography
      >
    </VButton>

    <div class="d-flex ms-2 flex-column justify-content-between">
      <VTypography font-weight="medium" class="file-picker__info"
        >Ограничения по размеру: {{ fileLimits }}</VTypography
      >
      <VTypography font-weight="medium" class="file-picker__info"
        >Форматы файла: {{ fileFormats.join(", ") }}</VTypography
      >
    </div>
  </div>
</template>

<script>
import VLoader from "@/components/ui/Loader";
import VButton from "@/components/buttons/VButton";
import VTypography from "@/components/ui/Typography";

export default {
  inheritAttrs: false,
  name: "FilePicker",
  components: { VLoader, VTypography, VButton },
  emits: {
    change: () => true,
  },
  props: {
    loading: Boolean,
    fileFormats: {
      type: Array,
      require: true,
      default: () => [],
    },
    fileLimits: {
      type: String,
      require: true,
    },
    disabled: Boolean,
  },
};
</script>

