<template>
  <div
    class="d-flex position-relative flex-column v-switch"
    :class="{ 'v-switch--disabled': disabled }"
  >
    <div
      v-if="!!modelValue"
      :style="{ width, transform }"
      class="position-absolute v-switch__indicator"
      :class="{ 'v-switch__indicator--disabled': disabled }"
    />

    <div class="d-flex flex-fill">
      <div
        :key="item.key"
        v-for="(item, index) in items"
        :class="{
          'v-switch__item--active': modelValue === item.key,
          'v-switch__item--disabled': disabled,
        }"
        class="d-flex v-switch__item"
      >
        <VPopover
          v-if="popoverText[item.key]"
          position="top-left"
          trigger-type="hover"
          :left-offset="index === 0 ? 122.5 : 64"
        >
          <template v-slot:content>
            <VButton
              ref="buttons"
              :disabled="disabled"
              class="px-1 user-select-none align-items-center justify-content-center v-switch__item-button"
              @click="click(item.key)"
            >
              <VTypography
                font-weight="heavy"
                class="text-uppercase v-switch__item-title"
                :class="{ 'v-switch__item-title--disabled': disabled }"
                >{{ item.title }}</VTypography
              >
            </VButton>
          </template>

          <template v-slot:popover>
            <PopoverContent :width="729">
              <VTypography font-weight="medium">
                {{ popoverText[item.key] }}
              </VTypography>
            </PopoverContent>
          </template>
        </VPopover>

        <VButton
          v-if="!popoverText[item.key]"
          ref="buttons"
          :disabled="disabled"
          class="px-1 user-select-none align-items-center justify-content-center"
          @click="click(item.key)"
        >
          <VTypography
            font-weight="heavy"
            :class="{ 'v-switch__item-title--disabled': disabled }"
            class="text-uppercase v-switch__item-title"
            >{{ item.title }}
          </VTypography>
        </VButton>
      </div>
    </div>
  </div>
</template>

<script>
import VTypography from "@/components/ui/Typography";
import VButton from "@/components/buttons/VButton";
import VPopover from "@/components/popover/Popover";
import PopoverContent from "@/components/popover/PopoverContent";

export default {
  name: "VSwitch",
  components: { PopoverContent, VPopover, VButton, VTypography },
  props: {
    items: Array,
    disabled: Boolean,
    modelValue: String,
    popoverText: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
      width: "0px",
      transform: "translateX(0)",
    };
  },
  mounted() {
    this.animate(0);
  },
  watch: {
    modelValue(value) {
      const valueIndex = this.items.findIndex((x) => x.key === value);

      const index = valueIndex >= 0 ? valueIndex : 0;

      this.animate(index);
    },
  },
  methods: {
    animate(index) {
      const prevIndex = index > 0 ? index - 1 : -1;

      const currentRef = this.$refs.buttons[index].$refs.vButton;
      const prevRef =
        prevIndex >= 0
          ? this.$refs.buttons[prevIndex].$refs.vButton
          : undefined;

      if (prevRef) {
        this.transform = `translateX(${prevRef?.clientWidth}px)`;
      } else {
        this.transform = "translateX(0)";
      }

      this.width = `${currentRef?.clientWidth + 2}px`;
    },
    click(key) {
      this.$emit("update:modelValue", key);
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/mixins.scss";
@import "~@/assets/styles/variables.scss";

.v-switch {
  height: 32px;
  border: 2px solid $gray1;
}

.v-switch--disabled {
  border-color: $gray4;
}

.v-switch__item-button {
  height: 29px;
}

.v-switch__item {
  cursor: pointer;

  &:not(:first-child) {
    border-left: 1px solid $gray1;
  }

  &:not(:last-child) {
    border-right: 1px solid $gray1;
  }
}

.v-switch__item--disabled {
  cursor: default;

  &:not(:first-child) {
    border-left: 1px solid $gray4;
  }

  &:not(:last-child) {
    border-right: 1px solid $gray4;
  }
}

.v-switch__item--active {
  & .v-switch__item-title {
    color: $white;
  }
}

.v-switch__item-title {
  @include transition(color);

  color: $gray1;
}

.v-switch__item-title--disabled {
  color: $gray4;
}

.v-switch__indicator {
  @include transition(transform);

  top: 0;
  bottom: 0;
  z-index: -1;
  background-color: $gray1;
}

.v-switch__indicator--disabled {
  background-color: $gray4;
}
</style>
