<template>
  <div class="d-flex inn-input">
    <div :key="index" v-for="(item, index) in value" class="d-flex">
      <input
        :value="item"
        :maxlength="1"
        ref="innInput"
        placeholder="0"
        @keydown="onKeyDown($event, index)"
        @change="item = $event.target.value"
        class="text-center align-self-center inn-input__field"
      />

      <div v-if="index < 11" class="align-self-end inn-input__divider" />
    </div>
  </div>
</template>

<script>
const initial = "";

export default {
  name: "InnInput",
  data() {
    const value = {};

    for (let i = 0; i < 12; i++) {
      value[i] = initial[i] || "";
    }

    return {
      value,
    };
  },
  methods: {
    onKeyDown(event, index) {
      if (event.key !== "Backspace" && event.code.indexOf("Digit") !== 0) {
        event.preventDefault();
      } else if (event.key === "Backspace") {
        const currentInput = this.$refs.innInput[index];
        const prevInput =
          index > 0 ? this.$refs.innInput[+index - 1] : undefined;

        if (currentInput) {
          currentInput.value = "";
        }

        if (prevInput) {
          prevInput.focus();
        }
      } else if (index < 12 && event.code.indexOf("Digit") === 0) {
        const currentInput = this.$refs.innInput[index];
        const nextInput = this.$refs.innInput[+index + 1];

        if (currentInput && !currentInput.value) {
          currentInput.value = event.key;
        }

        if (nextInput) {
          nextInput.focus();
        }
      }
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/variables.scss";

.inn-input {
  height: 45px;
  padding: 0 2px;
  border: 2px solid $gray2;
}

.inn-input__field {
  width: 30px;
  border: none;
  height: 30px;
  outline: none;
  background: none;
  color: $white;
}

.inn-input__divider {
  width: 2px;
  height: 5px;
  background-color: $gray2;
}
</style>
