<template>
  <div class="d-flex align-items-center technology-file-item">
    <FileIcon />
    <div class="d-flex flex-fill technology-file-item__title-container">
      <VTypography font-weight="medium" :class="titleClasses">{{
        fileName.name
      }}</VTypography>
      <VTypography
        class="flex-fill technology-file-item__ext"
        :class="titleClasses"
        font-weight="medium"
        v-if="fileName.ext"
        >{{ fileName.ext }}</VTypography
      >
    </div>

    <DeleteButton
      v-if="!disabled"
      @mouseenter="focus = true"
      @mouseleave="focus = false"
      @click="$emit('delete')"
    />
  </div>
</template>

<script>
import FileIcon from "@/components/icons/FileIcon";
import VTypography from "@/components/ui/Typography";
import DeleteButton from "@/components/buttons/DeleteButton";

export default {
  name: "TechnologyFileItem",
  components: { DeleteButton, FileIcon, VTypography },
  emits: {
    delete: Function,
  },
  props: {
    disabled: Boolean,
    file: {
      type: Object,
      require: true,
    },
  },
  data() {
    return {
      focus: false,
    };
  },
  computed: {
    titleClasses() {
      return {
        "technology-file-item__title--focus": this.focus,
      };
    },
    fileName() {
      const dotIndex = this.file?.name.lastIndexOf(".");

      if (dotIndex >= 0) {
        const longName = this.file?.name.length > 24;

        const ext = this.file?.name.substring(dotIndex + 1);
        const name = this.file?.name.substring(0, Math.min(dotIndex, 24));

        return {
          ext,
          name: [name, longName && "...", !!ext && "."]
            .filter(Boolean)
            .join(""),
        };
      }

      return {
        name: "Без имени",
      };
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/variables.scss";

.technology-file-item {
  max-width: 556px;
}

.technology-file-item__title-container {
  margin: 0 5px;
}

.technology-file-item__ext {
  color: $gray4;
}

.technology-file-item__title--focus {
  color: $red;
}
</style>
